/**
 * Home.tsx
 * Pgina principal que muestra:
 * - Hero section con mensaje principal
 * - Seccin de servicios destacados
 * - Seccin de estadsticas
 * - Testimonios de clientes
 * - Llamada a la accin (CTA)
 */
import React from 'react';
import { Container,  Box } from '@mui/material';
import { motion } from 'framer-motion';
import Testimonials from '../components/Testimonials';
import Team from '../components/Team';
import Hero from '../components/Hero';
import Projects from '../components/ProjectsSection';
import Stats from '../components/Stats';
import CTA from '../components/CTA';
import ServicesSection from '../components/ServicesSection';

const Home: React.FC = () => {

  // Configuracin de animaciones para elementos
  const fadeInUp = {
    initial: { opacity: 0, y: 60 },
    animate: { opacity: 1, y: 0 },
    transition: { duration: 0.6 }
  };

  return (
    <Box component="div">
      {/* Hero Section - Banner principal */}
      <Hero />

      {/* Seccin de Projects */}
      <Box component="section" sx={{ py: 8 }}>
        <Container maxWidth="lg">
          <motion.div {...fadeInUp}>
            <Projects />
          </motion.div>
        </Container>
      </Box>

      {/* Seccin de Estadsticas */}
      <Box 
        component="section" 
        sx={{ 
          py: 8, 
          backgroundColor: 'background.paper' 
        }}
      >
        <Container maxWidth="lg">
          <motion.div {...fadeInUp}>
            <Stats />
          </motion.div>
        </Container>
      </Box>

      {/* Seccin de Testimonios */}
      <Box component="section" sx={{ py: 8 }}>
        <Container maxWidth="lg">
          <motion.div {...fadeInUp}>
            <Testimonials />
          </motion.div>
        </Container>
      </Box>

      {/* Seccin de Llamada a la Accin (CTA) */}
      <Box 
        component="section" 
        sx={{ 
          py: 8, 
          backgroundColor: 'primary.main',
          color: 'primary.contrastText'
        }}
      >
        <Container maxWidth="lg">
          <motion.div {...fadeInUp}>
            <CTA />
          </motion.div>
        </Container>
      </Box>

      {/* Services Section */}
      <ServicesSection />

      {/* Team Section */}
      <Team />
    </Box>
  );
};

export default Home; 